import React from "react";
import { StyleSheet, View } from "react-native";

//<!--expo-->
import { Ionicons } from "@expo/vector-icons";

//<!--libs-->
import { ListItem, Icon, Divider, Text, Layout } from "@ui-kitten/components";
import Swipeout from "react-native-swipeout";

//<!--custom-->
import Colors from "../constants/Colors";
import { ticksToDate, monthNames } from "../utils/dateUtils";

const JournalItem = (props) => {
  const { item, index, navigateToDetails } = props;

  // swipeout-button
  let rightSwipeoutButtons = [
    {
      text: "Delete",
      component: (
        <View
          style={{ flex: 1, alignItems: "center", justifyContent: "center" }}
        >
          <Ionicons name="ios-trash" size={24} color="#fff" />
        </View>
      ),
      backgroundColor: Colors.bgDanger,
      onPress: () => console.log("DELETE"),
    },
  ];

  let leftSwipeoutBtns = [];
  if (item.isfavorite) {
    leftSwipeoutBtns.push({
      component: (
        <View
          style={{ flex: 1, alignItems: "center", justifyContent: "center" }}
        >
          <Ionicons name="ios-star" size={24} color="#fff" />
        </View>
      ),
      backgroundColor: Colors.bgSuccess,
      onPress: () => console.log("UNFAVORITE"),
    });
  } else {
    leftSwipeoutBtns.push({
      component: (
        <View
          style={{ flex: 1, alignItems: "center", justifyContent: "center" }}
        >
          <Ionicons name="ios-star-outline" size={24} color="#fff" />
        </View>
      ),
      backgroundColor: Colors.bgSuccess,
      onPress: () => console.log("UNFAVORITE"),
    });
  }

  return (
    <Swipeout
      autoClose={true}
      right={rightSwipeoutButtons}
      left={leftSwipeoutBtns}
    >
      <ListItem
        title={() => (
          <Text
            style={{ fontSize: 18, fontWeight: "700", paddingHorizontal: 8 }}
          >{`${item.title}`}</Text>
        )}
        //title={item.title}
        description={`${item.richcontent
          .replace(/\r?\n|\r/g, "")
          .slice(0, 70)}...`}
        onPress={() => {
          navigateToDetails(item);
        }}
        accessoryRight={() => {
          let ticks = Number(item.entrycreateddate);
          let dt = ticksToDate(ticks);

          return (
            <View style={styles.dateContainer}>
              <View style={styles.monthYearWrapper}>
                <Text category="s1">{dt.utcYear}</Text>
                <Text
                  category="p2"
                  appearance="hint"
                  style={{ fontWeight: "300" }}
                >
                  {dt.utcMonth}
                </Text>
              </View>
              {item.isfavorite ? (
                <Text style={{ color: Colors.favorite }} category="h1">
                  {dt.utcDate}
                </Text>
              ) : (
                <Text category="h1">{dt.utcDate}</Text>
              )}
            </View>
          );
        }}
      />
    </Swipeout>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  dateContainer: {
    flexDirection: "row",
    alignItems: "flex-end",
    justifyContent: "space-between",
  },
  monthYearWrapper: {
    alignItems: "flex-end",
    paddingHorizontal: 12,
  },
});

export default JournalItem;
