import React, { useState, useEffect, useMemo } from "react";
import { StyleSheet, SafeAreaView, View } from "react-native";

//<!-- redux-->
import { connect } from "react-redux";

//<!--libs-->
import styled from "styled-components/native";
import {
  Button,
  Icon,
  List,
  ListItem,
  Divider,
  Text,
  TopNavigation,
} from "@ui-kitten/components";

//<!--custom-->
import { ticksToDate, monthNames } from "../utils/dateUtils";
import EmptyView from "./../components/EmptyView";
import JournalItem from "./../components/JournalItem";

const extractKey = ({ guid }) => guid;

//<!--FAVORITES-->
const Favorites = ({ navigation, favorites }) => {
  useEffect(() => {}, []);

  const navigateToDetails = (item) => {
    navigation.navigate("Journal-Detail", { journal: item });
  };

  return (
    <View style={{ flex: 1 }}>
      <TopNavigation title="" alignment="center" />
      <Divider />
      {favorites.length > 0 ? (
        <List
          style={styles.container}
          keyExtractor={extractKey}
          data={favorites}
          renderItem={({ item, index }) => {
            return (
              <JournalItem
                item={item}
                index={index}
                navigateToDetails={navigateToDetails}
              />
            );
          }}
          ItemSeparatorComponent={Divider}
        />
      ) : (
        <EmptyView information={`No favorite journals are found..`} />
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

const DateContainer = styled.View`
  /* flex: 1; */
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
`;

const MonthYearWrapper = styled.View`
  align-items: flex-end;
  padding-right: 12px;
  padding-left: 12px;
`;

const mapStateToProps = (state) => ({
  favorites: state.dataStore.journals.filter((p) => p.isfavorite === true),
});

export default connect(mapStateToProps, {})(Favorites);
