import { ticksToDate } from "./../utils/dateUtils";
import EmptyView from "./../components/EmptyView";

const extractKey = ({ guid }) => guid;

//<!-- PHOTOS-->
const Photos = ({ navigation, photos }) => {
  useEffect(() => {
    return () => {
      // when data updates, do this
      // before Chart unmounts, do this
    };
  }, []);

  const renderPostItem = ({ item, index }) => {
    let ticks = Number(item.entrycreateddate);
    let dt = ticksToDate(ticks);
    return item.imagedata ? (
      <View style={styles.container}>
        <TouchableOpacity
          onPress={() => {
            navigation.navigate("Journal-Detail", { journal: item });
          }}
        >
          <ImageBackground
            source={{
              uri: `data:image/png;base64,${item.imagedata}`,
            }}
            style={styles.image}
          >
            {item.isfavorite ? (
              <Text category="h1" style={styles.favText}>
                {dt.utcDate}
              </Text>
            ) : (
              <Text category="h1" style={styles.text}>
                {dt.utcDate}
              </Text>
            )}

            <Text category="s1" style={[styles.text, { fontWeight: "300" }]}>
              {`${dt.utcMonth}, ${dt.utcYear}`}
            </Text>
          </ImageBackground>
        </TouchableOpacity>
      </View>
    ) : null;
  };

  return (
    <View style={{ flex: 1 }}>
      <TopNavigation title="" alignment="center" />
      <Divider />
      {photos.length > 0 ? (
        <List
          keyExtractor={extractKey}
          data={photos}
          //data={DATA}
          numColumns={2}
          renderItem={renderPostItem}
        />
      ) : (
        <EmptyView information={`No photos to display!`} />
      )}
    </View>
  );
};

