import React from "react";
import { View, SafeAreaView, ScrollView, Image } from "react-native";

//<!--redux-->
import { connect } from "react-redux";

//<!--libs-->
import {
  Layout,
  Text,
  Icon,
  Divider,
  TopNavigation,
  TopNavigationAction,
  Avatar,
} from "@ui-kitten/components";

//<!--custom-->
import Profile from "../components/Profile";

//<!--COMPONENT-->
const BackIcon = (props) => <Icon {...props} name="arrow-back" />;

//<!--ACCOUNT-->
const Account = ({ navigation, account }) => {
  const navigateBack = () => {
    navigation.goBack();
  };

  const BackAction = () => (
    <TopNavigationAction icon={BackIcon} onPress={navigateBack} />
  );

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <TopNavigation
        alignment="center"
        accessoryLeft={BackAction}
        title="Profile"
      />
      <Divider />
      <Layout style={{ flex: 1 }}>
        <View
          style={{
            height: "30.3%",
            alignItems: "center",
            justifyContent: "center",
            //borderWidth: 1,
            //borderColor: "red",
          }}
        >
          {/* image */}
          <View
            style={{
              height: 75,
              width: 75,
              borderRadius: 37.5,
              backgroundColor: "#C5CEE0",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            {/* style={{ fontWeight: "700", fontSize: 26 }} */}
            {account.profile_photo_url ? (
              <Avatar
                source={{ uri: account.profile_photo_url }}
                style={{ width: 74, height: 74 }}
              />
            ) : (
              <Text category="h3">{account.name.abbreviated_name}</Text>
            )}
          </View>
        </View>
        <Divider />
        <ScrollView>
          <Profile Hint="First Name" Value={account.name.given_name} />
          <Profile Hint="Last Name" Value={account.name.surname} />
          <Profile Hint="Email" Value={account.email} />
        </ScrollView>
      </Layout>
    </SafeAreaView>
  );
};

const mapStateToProps = (state) => ({
  account: state.dataStore.account,
});

export default connect(mapStateToProps, {})(Account);
