import React, { useState, useEffect } from "react";
import {
  SafeAreaView,
  StyleSheet,
  View,
  ScrollView,
  KeyboardAvoidingView,
  Image,
  TextInput,
  useInputState,
  Platform,
  ActivityIndicator,
} from "react-native";

// <!--expo-->

// <!--redux-->
import { connect } from "react-redux";

//<!-- libs-->
import _ from "lodash";
import {
  Divider,
  Icon,
  Layout,
  Text,
  Input,
  TopNavigation,
  TopNavigationAction,
  OverflowMenu,
  MenuItem,
} from "@ui-kitten/components";
import Markdown from "react-native-easy-markdown";

//<!--custom-->
import { ticksToDate, dateToTicks } from "./../utils/dateUtils";
import { updateJournal } from "./../redux/actions/dataAction";

//<!--COMPONENTS-->
const BackIcon = (props) => <Icon {...props} name="arrow-back" />;

const EditIcon = (props) => <Icon {...props} name="edit" />;

const DoneIcon = (props) => <Icon {...props} name="done-all-outline" />;

const MenuIcon = (props) => <Icon {...props} name="more-vertical" />;

const InfoIcon = (props) => <Icon {...props} name="info" />;

const LogoutIcon = (props) => <Icon {...props} name="log-out" />;

//<!--JOURNAL-DETAIL-->
const JournalDetail = ({ navigation, route, updateJournal }) => {
  const { journal } = route.params;

  const [activeJournal, setActiveJournal] = useState(null);
  const [date, setDate] = useState(null);
  const [menuVisible, setMenuVisible] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [content, setContent] = useState();

  useEffect(() => {
    console.log(`-------------------------------`);

    if (_.isEmpty(journal)) {
      toggleEditMode();
      console.log(`Journal not found, switching editmode to ${editMode}`);
    } else {
      console.log("Journal found!!");

      //set active journal
      let journalObj = {};
      Object.assign(journalObj, journal);
      setActiveJournal(journalObj);

      //set content
      if (journal.title) {
        setContent(`${journal.title}\n${journal.richcontent}`);
      } else {
        setContent(`${journal.richcontent}`);
      }

      //set date
      const ticks = Number(journal.entrycreateddate);
      const dt = ticksToDate(ticks);
      setDate(dt);
    }
  }, []);

  const toggleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  const toggleEditMode = () => {
    setEditMode(!editMode);

    //console.log(`edit-mode...${editMode}`);

    if (editMode) {
      console.log(`done-mode..`);
      onDone();
    }
  };

  const navigateBack = () => {
    navigation.goBack();
  };

  const onChangeContent = (content) => {
    setContent(content);
    //console.log(content);
  };

  //save journal
  const onDone = () => {
    let split = content.split("\n");
    console.log(`content-split-length::${split.length}`);

    if (split.length === 1) {
      activeJournal.richcontent = content;
    } else {
      activeJournal.title = split[0];
      activeJournal.richcontent = content.substring(content.indexOf("\n") + 1);
    }

    // console.log(
    //   `title:${activeJournal.title}\ncontent:${activeJournal.richcontent}`
    // );

    if (_.isEqual(journal, activeJournal)) {
      console.log(`No change detected!!`);
    } else {
      console.log(`Change detected!`);
      activeJournal.entrymodifieddate = dateToTicks(new Date());
      updateJournal(activeJournal);
    }
  };

  const renderMenuAction = () => (
    <TopNavigationAction icon={MenuIcon} onPress={toggleMenu} />
  );

  const BackAction = () => (
    <TopNavigationAction icon={BackIcon} onPress={navigateBack} />
  );

  const renderRightActions = () => (
    <>
      {editMode ? (
        <TopNavigationAction icon={DoneIcon} onPress={toggleEditMode} />
      ) : (
        <TopNavigationAction icon={EditIcon} onPress={toggleEditMode} />
      )}
      <OverflowMenu
        anchor={renderMenuAction}
        visible={menuVisible}
        onBackdropPress={toggleMenu}
      >
        <MenuItem accessoryLeft={InfoIcon} title="About" />
        <MenuItem accessoryLeft={LogoutIcon} title="Logout" />
      </OverflowMenu>
    </>
  );

  return (
    <SafeAreaView style={{ flex: 1 }}>
      {/* <!--header--> */}
      <TopNavigation
        alignment="center"
        title={
          date ? `${date.utcMonth} ${date.utcDate}, ${date.utcYear}` : "tock"
        }
        accessoryLeft={BackAction}
        accessoryRight={renderRightActions}
      />
      <Divider />

      {/* <!--MainView--> */}
      {activeJournal ? (
        <Layout style={{ flex: 1 }}>
          {editMode ? (
            <KeyboardAvoidingView behavior="padding" style={{ flex: 1 }}>
              {/* TextInput */}
              <TextInput
                multiline={true}
                enablesReturnKeyAutomatically={true}
                style={{
                  ...mdStyles.text,
                  flex: 1,
                  paddingHorizontal: 12,
                }}
                underlineColorAndroid="transparent"
                placeholder="start writing here.."
                rejectResponderTermination={true}
                value={content}
                onChangeText={onChangeContent}
              />
            </KeyboardAvoidingView>
          ) : (
            <ScrollView style={styles.scrollView}>
              {/* Image */}
              {activeJournal.imagedata ? (
                <Image
                  style={{
                    width: "100%",
                    // aspectRatio: 1,
                    resizeMode: "cover",
                    height: 400,
                  }}
                  source={{
                    uri: `data:image/png;base64,${activeJournal.imagedata}`,
                  }}
                />
              ) : null}

              <Layout
                style={{
                  paddingHorizontal: 20,
                }}
              >
                {/* title */}
                {activeJournal.title ? (
                  <View>
                    <Text style={styles.title} category="h4">
                      {activeJournal.title}
                    </Text>
                  </View>
                ) : null}

                {/* date */}
                {date && (
                  <Text
                    category="p1"
                    appearance="hint"
                    style={{
                      paddingVertical: 12,
                      // borderWidth: 1,
                      // borderColor: "blue",
                    }}
                  >
                    {`${date.utcMonth} ${date.utcDate}, ${date.utcYear}`}
                  </Text>
                )}

                {/* richcontent */}
                <Layout style={{ paddingBottom: 20 }}>
                  <Markdown useDefaultStyles markdownStyles={mdStyles}>
                    {activeJournal.richcontent}
                  </Markdown>
                </Layout>
              </Layout>
            </ScrollView>
          )}
        </Layout>
      ) : (
        <Layout
          style={{ flex: 1, alignItems: "center", justifyContent: "center" }}
        >
          <ActivityIndicator />
        </Layout>
      )}
    </SafeAreaView>
  );
};

