import React, { useState, useEffect, useContext } from "react";
import { View, StyleSheet, Alert, Linking, Platform } from "react-native";

//<!--expo-->
import { Ionicons } from "@expo/vector-icons";

//<!-- redux-->
import { connect } from "react-redux";
import { clearAllData } from "./../redux/actions/dataAction";
//<!--libs-->
import {
  List,
  ListItem,
  Divider,
  Layout,
  Button,
  Text,
  Icon,
  Toggle,
  TopNavigation,
} from "@ui-kitten/components";
import _ from "lodash";

//<!--custom-->
import { ThemeContext } from "../skin/theme-context";
import AppIcon from "./../components/AppIcon";

//<!--SETTINGS-->
const Settings = ({ navigation, journals, clearAllData }) => {
  const themeContext = useContext(ThemeContext);

  const [entriesCount, setEntriesCount] = useState(0);
  const [photosCount, setPhotosCount] = useState(0);
  const [favoritesCount, setFavoritesCount] = useState(0);
  const [tagsCount, setTagsCount] = useState(0);

  const [checked, setChecked] = useState(false);

  const onCheckedChange = (isChecked) => {
    setChecked(isChecked);
    themeContext.toggleTheme();
  };

  useEffect(() => {
    if (journals) {
      setEntriesCount(journals.length);

      const photos = journals.filter((p) => p.imagedata);
      if (_.size(photos) > 0) {
        setPhotosCount(photos.length);
      } else {
        setPhotosCount(0);
      }

      const favorites = journals.filter((p) => p.isfavorite);
      if (_.size(favorites) > 0) {
        setFavoritesCount(favorites.length);
      } else {
        setFavoritesCount(0);
      }
    }

    console.log(
      `[settings]::${entriesCount}::${photosCount}::${favoritesCount}`
    );
  }, [journals]);

  //Logo
  const LOGO = () => {
    return (
      <View
        style={{
          backgroundColor: "",
          height: "40%",
          paddingHorizontal: 50,
          justifyContent: "center",
        }}
      >
        <View
          style={{
            backgroundColor: "#9bdcfc",
            height: 60,
            width: 60,
            borderRadius: 30,
            alignItems: "center",
            justifyContent: "center",
            marginBottom: 20,
          }}
        >
          {/* <AppIcon style={{}} /> */}
          <Ionicons name="ios-stats" size={32} />
        </View>

        <View style={styles.statistics}>
          <View style={styles.metrics}>
            <Text category="label">JOURNALS</Text>
            <Text category="p1">{entriesCount}</Text>
          </View>

          <View style={styles.metrics}>
            <Text category="label">PHOTOS</Text>
            <Text category="p1">{photosCount}</Text>
          </View>
          <View style={styles.metrics}>
            <Text category="label">FAVORITES</Text>
            <Text category="p1">{favoritesCount}</Text>
          </View>
        </View>
      </View>
    );
  };

  const renderItemAccessory = () => (
    <Icon {...props} fill="#8F9BB3" name="chevron-right-outline" />
  );

  const renderItem = ({ item, index }) => (
    <ListItem
      title={`${item.title}`}
      description={`${item.description}`}
      accessoryLeft={(props) => (
        <Icon fill="#8F9BB3" {...props} name={item.icon} />
      )}
      accessoryRight={(props) => {
        return item.title === `Theme` ? (
          <Toggle checked={checked} onChange={onCheckedChange}></Toggle>
        ) : (
          <Icon {...props} fill="#8F9BB3" name="chevron-right-outline" />
        );
      }}
      onPress={() => {
        switch (item.title) {
          case "Account":
            navigation.navigate("Account");
            break;
          case "About":
            navigation.navigate(`About`);
            break;
          case "Contact":
            Linking.openURL(
              `mailto:support@getglimpses.com?subject=Hello there&body=`
            );
            break;
          case "Logout":
            if (Platform.OS === "web") return clearAllData();

            Alert.alert(
              "LOGOUT",
              "Do you really want to clear data?",
              [
                {
                  text: "Cancel",
                  onPress: () => console.log("Cancel Pressed"),
                  style: "cancel",
                },
                {
                  text: "OK",
                  onPress: () => {
                    console.log("OK Pressed");
                    console.log("Logout");
                    clearAllData();
                  },
                },
              ],
              { cancelable: true }
            );

            break;
        }
      }}
    />
  );
  return (
    <View style={{ flex: 1 }}>
      <TopNavigation title="" alignment="center" />
      <Divider />
      <Layout style={styles.container}>
        <LOGO />
        <Divider />
        <List
          data={MENU_ITEMS}
          renderItem={renderItem}
          ItemSeparatorComponent={Divider}
        />
      </Layout>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  statistics: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  metrics: {
    alignItems: "center",
  },
});

const MENU_ITEMS = [
  { icon: "person-outline", title: "Account", description: "Account linked" },
  { icon: "at-outline", title: "Contact", description: "Contact us" },
  { icon: "color-palette-outline", title: "About", description: "About us" },
  {
    icon: "flash-outline",
    title: "Theme",
    description: "Toggle theme",
  },
  {
    icon: "log-out-outline",
    title: "Logout",
    description: "Logout & Clear Data",
  },
];

const mapStateToProps = (state) => ({
  journals: state.dataStore.journals,
});

export default connect(mapStateToProps, { clearAllData })(Settings);
