import _ from "lodash";
import { Dropbox } from "dropbox";
import { Buffer } from "buffer";
const schema = require("../../utils/schema");
import { asyncForEach } from "./../../utils/commonUtils";

import { GET_DEFAULT_DATA, POST_DEFAULT_DATA, LOGOUT } from "./types";
import {
  SET_ACCESS_TOKEN,
  SET_ACCOUNT,
  SET_METADATA,
  GET_DATA,
  SET_DATA,
  UPDATE_DATA,
  DOWNLOAD_FILE,
  UPLOAD_FILE,
  DELETE_FILE,
  ERROR_GET_METADATA,
  ERROR_GET_DATA,
  ERROR_DOWNLOAD_FILE,
  ERROR_UPLOAD_FILE,
  ERROR_DELETE_FILE,
} from "./types";

var dbx = new Dropbox({
  fetch: fetch,
});

//<!------------------------------------UTILITY-FUNCTIONS----------------------------------
const debugMessageOnMetadata = (dataStore) => {
  console.log(`<----------------------BEGIN-GETSTORE-----------------------`);

  console.log(
    JSON.stringify(
      _.isEmpty(dataStore.metadata)
        ? `NO METADATA`
        : `METADA.ENTRIES::${dataStore.metadata.entries.length}`
    )
  );
  console.log(`------------------------END-GETSTORE------------------------>`);
};

const getFileMetadata = async (path) => {
  try {
    return await dbx.filesDownload({
      path: `${path}`,
    });
  } catch (error) {
    console.log(`[Error downloading the file]=>${error.error} ❎`);
  }
};

const processEntries = (entries, type) => async (dispatch) => {
  try {
    asyncForEach(entries, async (entry) => {
      console.log(`downloading..${entry.name}`);
      let metadata = await getFileMetadata(entry.path_lower);

      const reader = new FileReader();
      reader.onloadend = (e) => {
        let result = reader.result.split(",", 2)[1];
        let buff = new Buffer(result, "base64");
        const entryData = schema.Entry.deserializeBinary(new Uint8Array(buff));

        dispatch({
          type,
          payload: entryData.toObject(),
        });
      };
      reader.readAsDataURL(metadata.fileBlob);
    });
  } catch (error) {
    console.log(`ERROR[processEntries]=>${error.error} ❎`);
  }
};

const getEntriesToBeProcessed = (
  serverMetadataEntries,
  clientMetadataEntries
) => {
  try {
    let hasMatchingEntry = false;
    let entriesToDownload = [];
    let entriesToUpdate = [];

    serverMetadataEntries?.forEach((serverEntry) => {
      hasMatchingEntry = false;

      clientMetadataEntries?.forEach((clientEntry) => {
        if (serverEntry.id === clientEntry.id) {
          hasMatchingEntry = true;

          const millis =
            Date.parse(serverEntry.server_modified) -
            Date.parse(clientEntry.server_modified);

          if (millis > 0) entriesToUpdate = [...entriesToUpdate, serverEntry];
        }
      });

      if (!hasMatchingEntry)
        entriesToDownload = [...entriesToDownload, serverEntry];
    });

    return [entriesToUpdate, entriesToDownload];
  } catch (error) {
    console.log(`ERROR[getEntriesToBeProcessed]=>${error.error} ❎`);
  }
};

const getServerMetadata = async () => {
  try {
    const serverMetadata = await dbx.filesListFolder({
      path: "",
      limit: 5,
    });

    //get only files with .entry extension
    serverMetadata.entries = serverMetadata.entries.filter((p) => {
      return (p[".tag"] === "file") & p.name.endsWith(".entry");
    });

    console.log(
      `Server metadata entries count=>${serverMetadata.entries.length}`
    );
    return serverMetadata;
  } catch (error) {
    console.log(`ERROR[getServerMetadata]=>${error.error} ❎`);
  }
};

//<!----------------------------------------ACTIONS----------------------------------------

export const getJournals = () => async (dispatch, getState) => {
  dbx.setAccessToken(getState().dataStore.accessToken);

  try {
    const dataStore = getState().dataStore;
    debugMessageOnMetadata(dataStore);

    const serverMetadata = await getServerMetadata();

    const serverMetadataEntries = serverMetadata.entries;
    const clientMetadataEntries = dataStore.metadata?.entries;

    if (serverMetadataEntries?.length > 0) {
      const [entriesToUpdate, entriesToDownload] = getEntriesToBeProcessed(
        serverMetadataEntries,
        clientMetadataEntries
      );

      console.log(`[to-download]=>${entriesToDownload?.length}`);
      console.log(`[to-update]=>${entriesToUpdate?.length}`);

      if (entriesToDownload)
        dispatch(processEntries(entriesToDownload, SET_DATA));

      if (entriesToUpdate)
        dispatch(processEntries(entriesToUpdate, UPDATE_DATA));
    }

    if (serverMetadata)
      dispatch({
        type: SET_METADATA,
        payload: serverMetadata,
      });
  } catch (error) {
    console.log(`ERROR[getJournals]=>${JSON.stringify(error)}`);
    return {
      type: ERROR_GET_METADATA,
    };
  }
};

export const updateJournal = (journalItem) => async (dispatch) => {
  try {
    const entry = new schema.Entry();
    entry.setGuid(journalItem.guid);
    entry.setEntrycreateddate(journalItem.entrycreateddate);
    entry.setEntrymodifieddate(journalItem.entrymodifieddate);
    entry.setTitle(journalItem.title);
    entry.setRichcontent(journalItem.richcontent);
    entry.setClientrevision(journalItem.clientrevision);
    entry.setImagedata(journalItem.imagedata);
    entry.setClientrev(journalItem.clientrev);
    entry.setIsfavorite(journalItem.isfavorite);
    entry.setTagsList(journalItem.tagsList);
    entry.setLatitude(journalItem.latitude);
    entry.setLongitude(journalItem.longitude);
    entry.setCelcius(journalItem.celcius);
    entry.setFahrenheit(journalItem.fahrenheit);
    entry.setRelativehumidity(journalItem.relativehumidity);
    entry.setWindspeedinkph(journalItem.windspeedinkph);

    let serialized = entry.serializeBinary();

    const response = await dbx.filesUpload({
      path: `/${journalItem.guid}.entry`,
      contents: serialized,
      mode: {
        ".tag": "overwrite",
      },
      autorename: false,
      mute: false,
    });
    //console.log(`Response::${JSON.stringify(response)}`);

    dispatch({
      type: UPDATE_DATA,
      payload: journalItem,
    });
  } catch (error) {
    console.log(`CUSTOM-UPLOAD-ERR:: ${error.message}`);
  }
};

export const setAccessToken = (result) => async (dispatch) => {
  dbx.accessToken = result.access_token;

  dispatch({
    type: SET_ACCESS_TOKEN,
    payload: result.access_token,
  });

  const account = await dbx.usersGetAccount({
    account_id: result.account_id,
  });

  dispatch({
    type: SET_ACCOUNT,
    payload: account,
  });
};

