import React from "react";
import { View, StyleSheet, Linking, SafeAreaView } from "react-native";

//<!--expo-->
import { Ionicons } from "@expo/vector-icons";
import * as WebBrowser from "expo-web-browser";

//<!--libs-->
import {
  List,
  ListItem,
  Divider,
  Layout,
  Button,
  Text,
  Icon,
  TopNavigation,
  TopNavigationAction,
} from "@ui-kitten/components";

//<!--custom-->
import AppIcon from "./../components/AppIcon";

//<!--COMPONENT-->
const BackIcon = (props) => <Icon {...props} name="arrow-back" />;

//<!--ABOUT-->
const About = ({ navigation }) => {
  const navigateBack = () => {
    navigation.goBack();
  };

  const BackAction = () => (
    <TopNavigationAction icon={BackIcon} onPress={navigateBack} />
  );

  const _handleLinkClick = async (url) => {
    console.log(`[ABOUT_SCREEN_CLICK]::${url}`);

    if (url.includes("twitter")) {
      try {
        return await Linking.openURL(url);
      } catch (e) {
        return await WebBrowser.openBrowserAsync(
          "https://twitter.com/" + `getglimpses`
        );
      }
    } else {
      await WebBrowser.openBrowserAsync(url);
    }
  };

  const renderItemAccessory = (props) => (
    <Icon {...props} fill="#8F9BB3" name="chevron-right-outline" />
  );

  const renderItem = ({ item, index }) => (
    <ListItem
      title={`${item.title}`}
      description={`${item.description}`}
      accessoryLeft={(props) => (
        <Icon fill="#8F9BB3" {...props} name={item.icon} />
      )}
      accessoryRight={renderItemAccessory}
      onPress={() => {
        _handleLinkClick(item.url);
      }}
    />
  );
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <TopNavigation
        alignment="center"
        accessoryLeft={BackAction}
        title="About glimpses"
      />
      <Divider />
      <Layout style={styles.container}>
        <View
          style={{
            height: "30.3%",
            alignItems: "center",
            justifyContent: "center",
            marginVertical: 20,
            // borderWidth: 1,
            // borderColor: "red",
          }}
        >
          <View
            style={{
              height: 72,
              width: 72,
              borderRadius: 36,
              backgroundColor: "#9bdcfc",
              alignItems: "center",
              justifyContent: "center",
              paddingBottom: 6,
              marginBottom: 3,
            }}
          >
            <AppIcon height="48" width="48" />
          </View>
          <Text
            appearance="hint"
            style={{ fontWeight: "300", fontSize: 14, marginTop: 3 }}
          >
            Version 1.0.0
          </Text>
        </View>
        <Divider />
        <List
          data={MENU_ITEMS}
          renderItem={renderItem}
          ItemSeparatorComponent={Divider}
        />
      </Layout>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: "flex-start",
    // borderWidth: 1,
    // borderColor: "green",
  },
});

