//<!--custom-->
import { ticksToDate, monthNames } from "../utils/dateUtils";
import EmptyView from "../components/EmptyView";
import Colors from "./../constants/Colors";
import JournalItem from "../components/JournalItem";

const extractKey = ({ guid }) => guid;

//<!--JOURNALS-->
const Journals = (props) => {
  const { navigation, getJournals, journals } = props;

  useEffect(() => {
    getJournals();
  }, []);

  const navigateToDetails = (item) => {
    navigation.navigate("Journal-Detail", { journal: item });
    //navigation.navigate("Journal-Detail", { journal: null });
  };

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <TopNavigation
        title={() => <Text category="h4">{`Timeline`}</Text>}
        alignment="center"
      />
      <Divider />
      <Layout style={{ flex: 1 }}>
        {journals.length > 0 ? (
          <List
            style={styles.container}
            data={journals}
            keyExtractor={extractKey}
            //data={DATA}
            renderItem={({ item, index }) => {
              return (
                <JournalItem
                  item={item}
                  index={index}
                  navigateToDetails={navigateToDetails}
                />
              );
            }}
            ItemSeparatorComponent={Divider}
          />
        ) : (
          <EmptyView
            information={`No journals are found,\n please press '+' to create new`}
          />
        )}
      </Layout>
    </SafeAreaView>
  );
};

