import React from "react";

import {
  makeRedirectUri,
  ResponseType,
  useAuthRequest,
  Prompt,
} from "expo-auth-session";

const useDropboxAuth = () => {
  // const { setAsync } = React.useContext(AuthProvider.Context);

  // Endpoint
  const discovery = {
    authorizationEndpoint: "https://www.dropbox.com/oauth2/authorize",
    tokenEndpoint: "https://www.dropbox.com/oauth2/token",
  };

  // Request
  const [request, response, promptAsync] = useAuthRequest(
    {
      clientId: "5jtw1f8hfieyk8g",
      // There are no scopes so just pass an empty array
      scopes: [],
      extraParams: {
        force_reapprove: true,
      },
      // Dropbox doesn't support PKCE
      usePKCE: false,
      // Implicit auth is universal, `.Code` will only work in native with `useProxy: true`.
      responseType: ResponseType.Token,
      // For usage in managed apps using the proxy
      redirectUri: makeRedirectUri({
        preferLocalhost: true,
        path: "redirect",
        // For usage in bare and standalone
        native: "com.getglimpses.app://redirect",
      }),
    },
    discovery
  );
  console.log(request?.redirectUri);

  React.useEffect(() => {
    if (response?.type === "success") {
      //setAsync(response.params.access_token);
      console.log(`[useDropboxAuth]${response.params.access_token}`);
    }
  }, [response]);

  return [request, response, promptAsync];
};

export default useDropboxAuth;
