import {
  GET_DEFAULT_DATA,
  POST_DEFAULT_DATA,
  LOGOUT,
} from "./../actions/types";
import {
  SET_ACCESS_TOKEN,
  SET_ACCOUNT,
  SET_METADATA,
  GET_DATA,
  SET_DATA,
  UPDATE_DATA,
  DOWNLOAD_FILE,
  UPLOAD_FILE,
  DELETE_FILE,
  ERROR_GET_METADATA,
  ERROR_GET_DATA,
  ERROR_DOWNLOAD_FILE,
  ERROR_UPLOAD_FILE,
  ERROR_DELETE_FILE,
} from "./../actions/types";

import { ticksToDate, monthNames } from ",/../../utils/dateUtils";

const initialState = {
  data: [],
  metadata: null,
  journals: [],
  accessToken: "",
  account: {},
};

const dataReducer = (state = initialState, action) => {
  //console.log(`xxxxxxxxx ${action.type}xxxxxxxxx`);
  switch (action.type) {
    case SET_ACCESS_TOKEN:
      return {
        ...state,
        accessToken: action.payload,
      };
      break;

    case SET_ACCOUNT:
      return {
        ...state,
        account: action.payload,
      };
      break;

    case SET_METADATA:
      return {
        ...state,
        metadata: action.payload,
      };

    case GET_DATA:
      return {
        ...state,
        journals: action.payload,
      };
      break;
    case SET_DATA:
      return {
        ...state,
        journals: [...state.journals, action.payload].sort(
          (a, b) =>
            Date.parse(ticksToDate(Number(b.entrycreateddate)).date) -
            Date.parse(ticksToDate(Number(a.entrycreateddate)).date)
        ),
      };
      break;

    case UPDATE_DATA:
      return {
        ...state,
        journals: state.journals.map((journalItem) =>
          journalItem.guid === action.payload.guid
            ? action.payload
            : journalItem
        ),
      };
      break;

    case DOWNLOAD_FILE:
      break;

    case UPLOAD_FILE:
      break;

    case DELETE_FILE:
      break;

    case ERROR_GET_METADATA:
    case ERROR_GET_DATA:
    case ERROR_DOWNLOAD_FILE:
    case ERROR_UPLOAD_FILE:
    case ERROR_DELETE_FILE:
      return {
        state,
      };
      break;

    //==============================
    case LOGOUT:
      return (state = {
        data: [],
        metadata: {},
        journals: [],
        accessToken: "",
        account: {},
      });
      break;
    case GET_DEFAULT_DATA:
      return {
        ...state,
        data: action.payload,
      };
      break;

    case POST_DEFAULT_DATA:
      return {
        ...state,
        accessToken: action.payload,
      };
    default:
      return state;
      break;
  }
};

export default dataReducer;
