import React, { useState, useEffect } from "react";
import { StyleSheet, Platform, View } from "react-native";

//<!--expo-->
import { Ionicons } from "@expo/vector-icons";
//import * as AuthSession from "expo-auth-session";
//import * as WebBrowser from "expo-web-browser";
import {
  makeRedirectUri,
  ResponseType,
  useAuthRequest,
  Prompt,
} from "expo-auth-session";
import { maybeCompleteAuthSession } from "expo-web-browser";

//<!--redux-->
import Constants from "expo-constants";
import { connect } from "react-redux";

//<!--libs-->
import { Layout, Button, Text, Icon } from "@ui-kitten/components";

//<!--custom-->
import { Dropbox } from "dropbox";
import useDropboxAuth from "./../hooks/useDropboxAuth";
import { parseQueryString, getUrlParams } from "./../utils/webUtils";
import { setAccessToken } from "../redux/actions/dataAction";
import AppIcon from "./../components/AppIcon";

if (Platform.OS === "web") {
  Constants.manifest.id = `@aneesahammed/${Constants.manifest.slug}`;
  maybeCompleteAuthSession();
}

// <!-- WELCOME-->
const Welcome = ({ navigation, setAccessToken }) => {
  const [request, res, promptAsync] = useDropboxAuth();

  useEffect(() => {
    if (res) {
      //console.log(`[Welcome]${JSON.stringify(res)}`);
      if (res.type == "success") {
        const result = {
          access_token: res.params.access_token,
          account_id: res.params.account_id,
        };
        setAccessToken(result);
      }
    }
  }, [res]);

  const _handleLogin = () => {
    let response = promptAsync();
  };

  const DropboxIcon = (props) => (
    <Ionicons
      name="logo-dropbox"
      size={32}
      color="#383838"
      style={{ marginRight: 20 }}
    />
  );

  return (
    <Layout style={{ flex: 1, alignItems: "center", justifyContent: "center" }}>
      <AppIcon height="100" width="100" />
      <Text category="h1">glimpses</Text>
      <Text
        appearance="hint"
        style={{
          textAlign: "center",
          fontWeight: "300",
          fontSize: 17,
          paddingVertical: 12,
        }}
      >
        {" "}
        Capture your memories {"\n"}to remember life..{" "}
      </Text>
      <Button
        style={styles.button}
        status="primary"
        accessoryLeft={DropboxIcon}
        onPress={_handleLogin}
      >
        Log in with Dropbox
      </Button>
    </Layout>
  );
};

const styles = StyleSheet.create({
  button: {
    margin: 2,
    justifyContent: "center",
    width: 300,
  },
});

export default connect(null, { setAccessToken })(Welcome);
